body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body.simple-login-app {
  background-color: transparent;
}
.route-holder{
  min-height: calc(100vh - 100px);
}
.site-footer{
  display: flex;
  align-items: center;
  justify-content: center;
  background: #606c88;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #3f4c6b, #606c88); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: #fff;
}
.app-background{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background: #7F7FD5;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #91EAE4, #86A8E7, #7F7FD5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.site-header,
.site-footer{
  height: 50px;
}
.site-header .nav{
  height: 100%;
}
.site-header a {
	color: inherit;
}
.site-header .nav-tabs{
  border : none;
  background: #606c88;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #3f4c6b, #606c88); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: #fff;
}
.site-header .nav-tabs .nav-item{
	margin-bottom: 0
}
.site-header .nav .nav-link{
  display: flex;
  align-items: center;
  height: 100%;
  border: none;
  border-radius : 0.25rem;
}
.site-header .nav-tabs .nav-link:hover,
.site-header .nav-tabs .nav-link.active,
.site-header .nav-tabs .nav-item.show .nav-link{
	background: #0F2027;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	color: #fff;
}
.route-holder.social{
	background: #eaebef;
}
.white-card{
	background-color: #fff;
	border-radius: 6px;
	overflow: hidden;
	box-shadow: 0 0 6px 0 rgba(0,0,0,0.32);
	padding: 15px;
	margin-top: 20px;
}
.white-card:first-child{
	margin-top: 0;
}
.social-posts-wrapper{
	width: 50%;
	padding: 20px 0;
	margin: auto;
}
.shimmer-container{
	width: 100%;
}
.shimmer-loader{
	background-color: #f2f2f2;
	border-radius: 8px;
}
.shimmer-container .shimmer-image{
	height: 480px;
	margin-bottom: 10px;
}
.shimmer-container .shimmer-text{
	height: 15px;
	margin-top: 10px;
}
.shimmer-container .first-text{
	width: 75%;
}
.shimmer-container .second-text{
	width: 90%;
}
.shimmer-container .third-text{
	width: 50%;
}
